<template>
	<div class="wrapper">
		<v-form
			ref="form"
			@submit.prevent="submitForm"
		>
			<v-container>
				<v-row>
					<v-col cols="12">
						<v-text-field
							v-model.trim="clientFields.username"
							:rules="generalRules"
							label="Логин"
							required
							@keydown.space.prevent
						/>
						<div id="general-fields">
							<v-text-field
								v-model.trim="clientFields.password"
								:rules="generalRules"
								label="Пароль"
								required
								@keydown.space.prevent
							/>
							<v-text-field
								v-model.trim="clientFields.company_name"
								:rules="generalRules"
								label="Название компании"
								required
							/>
							<v-text-field
								v-model.trim="clientFields.bin"
								:rules="generalRules"
								label="БИН компании"
								required
							/>
							<v-text-field
								v-model.trim="clientFields.sender_name"
								:rules="generalRules"
								label="Имя отправителя"
								required
							/>
						</div>
					</v-col>
				</v-row>
				<v-row>
					<v-col cols="12">
						<v-btn
							type="submit"
							color="primary"
						>
							Отправить
						</v-btn>
					</v-col>
				</v-row>
			</v-container>
		</v-form>
		<vue-element-loading 
			:active="isLoading"
			:background-color="'rgba(255, 255, 255, .2)'"
			:color="'blue'"
		/>
	</div>
</template>

<script>
import { HTTP } from '@/helpers/httpCommon';
export default {
	data() {
		return {
			clientFields: {
				username: '',
				password: '',
				user_role: 'client',
				company_name: '',
				bin: '',
				sender_name: '',
				is_active: true
			},
			isLoading: false,
			generalRules: [
				v => !!v || '*Обязательное поле',
			],
		};
	},
	methods: {
		validate () {
			return this.$refs.form.validate();
		},
		reset () {
			this.$refs.form.reset();
		},
		submitForm() {
			if(this.validate()) {
				const clientFields = JSON.parse(JSON.stringify(this.clientFields));
				this.isLoading = true;
				HTTP.post('user/create', clientFields)
					.then(() => {
						this.$store.commit('setClient', clientFields);
						this.$store.commit('setActiveDashboardCategory', 'list');
						this.$toast.success('Клиент успешно создан!', {
							timeout: 2000
						});
						this.reset();
					})
					.catch(error => {
						this.$toast.error(error.message, {
							timeout: 2000
						});
					})
					.finally(() => {
						this.isLoading = false;	
					});
			}
		}
	}
};
</script>
